<template>
  <div class="container">
    <headerNavBack :centerTitle="centerTitle"></headerNavBack>
    <div class="area">
      <div>
        <span>QQ号</span>
        <input placeholder="请输入QQ" type="text" v-model="keyword" autocomplete="off">
      </div>
    </div>
    <div class="submitBtn">
      <button @click="submitBtn">提交</button>
    </div>
  </div>
</template>
<script>
  import headerNavBack from '@/components/headerNavBack'
  export default {
    components: {
      headerNavBack,
    },
    data() {
      return {
        user: {},
        keyword: '',
        bool: true,
        centerTitle: 'QQ找回密码',
      }
    },
    methods: {
      back() {
        this.$router.go(-1)
      },
      submitBtn() {
        if (!this.keyword || this.keyword.length == 0 || this.keyword.charAt(0) == '') {
          this.$Toast({
            message: '请输入QQ号码',
            position: 'middle',
            duration: 500
          })
          return
        }
        this.findUserByQQ().then(data => {
          if (data.data.status == 'success') {
            this.sendMail().then(result => {
              if (result.data.result == '发送成功') {
                this.$Toast({
                  message: '发送成功',
                  position: 'middle',
                  duration: 500
                })
                setTimeout(() => {
                  this.$router.push('/mine')
                }, 500)
              }
            }).catch(err => {
              console.log(err)
            })
          }
        }).catch(err => {
          console.log(err)
        })
      },
      findUserByQQ() {
        return new Promise((resovle, reject) => {
          this.axios.get('/api/findUserByQQ', {
            params: {
              type: 'QQ',
              keyword: this.keyword
            }
          }).then((result) => {
            resovle(result)
          }).catch((err) => {
            console.log(err)
          });
        })
      },
      sendMail() {
        return new Promise((resovle, reject) => {
          this.axios.post('/api/reset-pwd', {
            qq: this.keyword
          }).then((result) => {
            resovle(result)
          }).catch((err) => {
            console.log(err)
          });
        })
      }
    },
    mounted() {}
  }
</script>
<style lang="less" scoped>
  .container {
    background-color: #eee;
  } // 头部
  .header {
    display: flex;
    height: 2.5rem;
    background-color: #fff;
    div {
      flex: 2;
      padding-left: 1.25rem;
      height: 2.5rem;
    }
    span {
      flex: 3.333;
      display: inline-block;
      text-align: center;
      height: 2.5rem;
      line-height: 2.5rem;
      &:nth-child(1) {
        color: #999;
        text-align: left;
        text-indent: 1em;
      }
      &:nth-child(2) {
        font-size: 1.125rem;
      }
      &:nth-child(3) {
        text-align: right;
        padding-right: 1em;
        color: #44b336;
        box-sizing: border-box;
      }
    }
  } // 头部
  .area {
    padding: 1.25rem .625rem;
    &>div {
      padding-left: .625rem;
    }
    &>div {
      background-color: #fff;
      box-sizing: border-box;
      margin-bottom: .625rem;
      display: flex;
      span {
        font-size: 1rem;
        display: inline-block;
        height: 2.5rem;
        text-align: center;
        line-height: 2.5rem;
        flex: 2;
      }
      input {
        border: none;
        outline: none;
        height: 2.5rem;
        text-indent: 1em;
        flex: 8;
      }
    }
  }
  .submitBtn {
    width: 100%;
    text-align: center;
    &>button {
      width: 90%;
      height: 3.125rem;
      background: #6dce67;
      text-align: center;
      line-height: 3.125rem;
      color: #fff;
      font-size: 1.25rem;
      border: none;
      outline: none;
      border-radius: .625rem;
    }
  }
</style>



